.main {
	width: 100%;
	height: 100vh;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative; }

.main_text {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	text-align: center; }

.nav-link {
	font-size: 14px;
	@include mont; }

.custom {
	padding: 25px 0 !important;
	background-color: $white !important;
	@include sm {
		margin: 0 3%; } }

.navbar-cust {
	padding-top: 40px !important;
	z-index: 99;
	@include sm {
		padding-top: 15px !important; } }

.main_text {
	width: 100%;
	&__topic {
		@include topic; } }

