@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "component/header";
@import "component/mainfooter";

.singl {
    @include block;
    min-height: 700px;
    align-items: center !important;
    background-color: $silver; }

.singl {
    h1,p {
        padding-right: 50%;
        @include sm {
            padding-right: 40%; } }
    p {
        span {
            @include mont; } } }

.sing-main-text {
    padding-top: 30px;
    padding-bottom: 45px; }

.nav-line {
    width: 100%;
    height: 100px;
    background-color: $white;
    display: flex;
    align-items: center;
    &__modul {
        text-align: center; }
    &__next {
        text-align: right; }
    @include sm {
        display: none; } }

.nav-line__prev,.nav-line__next {
    a {
        text-transform: uppercase !important;
        font-size: 14px !important;
        @include mont; } }
