//Color var
$black: #101010;
$Mcolor: #00CAC4;
$silver: #F5F5F5;
$white: #ffffff;
//Mixins
@mixin mont {
	font-family: 'Montserrat' !important;
	font-weight: 500 !important; }

@mixin nunito {
	font-family: 'Nunito Sans' !important;
	font-weight: 400 !important; }

@mixin btn {
	color: #ffffff !important;
	font-size: 14px;
	padding: 20px 45px;
	background-color: $Mcolor;
	text-transform: uppercase;
	transition: 1s ease !important;
	@include mont;
	@include sm {
		padding: 15px 40px; }
	&:hover {
		background-color: #00B4AE !important;
		box-shadow: 5px 5px 20px 1px rgba(166,166,166,0.82) !important;
		border-radius: 5%;
		transition: 1s ease !important; } }

@mixin topic {
	p {
		font-size: 16px !important;
		color: #797979 !important;
		@include sm {
			padding-left: 3%  !important;
			padding-right: 3% !important; } }
	h2 {
		font-size: 2.3em !important; } }

@mixin block {
	width: 100%;
	display: flex;
	justify-content: center;
	@include sm {
		padding: 0% !important; } }

@mixin block-center {
	text-align: center;
	align-items: center;
	h1 {
		padding-bottom: 30px !important; } }
//Conteiner
.con {
	max-width: 1200px;
	margin: 0 auto; }

body {
	font-size: 16px;
	overflow-x: hidden;
	@include nunito; }

h1,h2,h3,h4,h5,h6 {
	@include mont; }
//Link
a {
	color: $black !important;
	transition: .5s ease !important; }
a:hover {
	text-decoration: none !important;
	color: $Mcolor !important; }

// Button
.custom-btn {
	a {
		@include btn; } }

.custom-btn-type {
	@include btn; }


// Style topic
.work__inf,.contact-form__inf,.main_text {
	@include topic;
	p {
		padding: 35px 15% !important; } }

.about__inf {
	@include topic;
	p {
		padding: 35px 15% 0 15%  !important; } }

