.con {
  max-width: 1200px;
  margin: 0 auto;
}

body {
  font-size: 16px;
  overflow-x: hidden;
  font-family: 'Nunito Sans' !important;
  font-weight: 400 !important;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}

a {
  color: #101010 !important;
  transition: .5s ease !important;
}

a:hover {
  text-decoration: none !important;
  color: #00CAC4 !important;
}

.custom-btn a {
  color: #ffffff !important;
  font-size: 14px;
  padding: 20px 45px;
  background-color: #00CAC4;
  text-transform: uppercase;
  transition: 1s ease !important;
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}

@media only screen and (max-width: 767px) {
  .custom-btn a {
    padding: 15px 40px;
  }
}

.custom-btn a:hover {
  background-color: #00B4AE !important;
  box-shadow: 5px 5px 20px 1px rgba(166, 166, 166, 0.82) !important;
  border-radius: 5%;
  transition: 1s ease !important;
}

.custom-btn-type {
  color: #ffffff !important;
  font-size: 14px;
  padding: 20px 45px;
  background-color: #00CAC4;
  text-transform: uppercase;
  transition: 1s ease !important;
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}

@media only screen and (max-width: 767px) {
  .custom-btn-type {
    padding: 15px 40px;
  }
}

.custom-btn-type:hover {
  background-color: #00B4AE !important;
  box-shadow: 5px 5px 20px 1px rgba(166, 166, 166, 0.82) !important;
  border-radius: 5%;
  transition: 1s ease !important;
}

.work__inf p, .contact-form__inf p, .main_text p {
  font-size: 16px !important;
  color: #797979 !important;
}

@media only screen and (max-width: 767px) {
  .work__inf p, .contact-form__inf p, .main_text p {
    padding-left: 3%  !important;
    padding-right: 3% !important;
  }
}

.work__inf h2, .contact-form__inf h2, .main_text h2 {
  font-size: 2.3em !important;
}

.work__inf p, .contact-form__inf p, .main_text p {
  padding: 35px 15% !important;
}

.about__inf p {
  font-size: 16px !important;
  color: #797979 !important;
}

@media only screen and (max-width: 767px) {
  .about__inf p {
    padding-left: 3%  !important;
    padding-right: 3% !important;
  }
}

.about__inf h2 {
  font-size: 2.3em !important;
}

.about__inf p {
  padding: 35px 15% 0 15%  !important;
}

.main {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.main_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.nav-link {
  font-size: 14px;
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}

.custom {
  padding: 25px 0 !important;
  background-color: #ffffff !important;
}

@media only screen and (max-width: 767px) {
  .custom {
    margin: 0 3%;
  }
}

.navbar-cust {
  padding-top: 40px !important;
  z-index: 99;
}

@media only screen and (max-width: 767px) {
  .navbar-cust {
    padding-top: 15px !important;
  }
}

.main_text {
  width: 100%;
}

.main_text__topic p {
  font-size: 16px !important;
  color: #797979 !important;
}

@media only screen and (max-width: 767px) {
  .main_text__topic p {
    padding-left: 3%  !important;
    padding-right: 3% !important;
  }
}

.main_text__topic h2 {
  font-size: 2.3em !important;
}

.main-footer {
  background-color: #0C0C0C;
  color: #ffffff;
  padding: 80px 0;
}

@media only screen and (max-width: 768px) {
  .main-footer__inf {
    text-align: center;
  }
}

@media only screen and (max-width: 767px) {
  .main-footer__menu {
    padding: 35px 0;
  }
}

@media only screen and (max-width: 768px) {
  .main-footer__menu {
    margin: 35px 0;
  }
}

.main-footer__menu nav ul {
  width: 50%;
  float: left;
}

.main-footer__menu nav, .main-footer__link nav {
  text-align: left;
}

@media only screen and (max-width: 768px) {
  .main-footer__menu nav, .main-footer__link nav {
    text-align: center;
  }
}

.main-footer__menu nav ul li a, .main-footer__link nav ul li a {
  color: #ffffff !important;
}

.singl {
  width: 100%;
  display: flex;
  justify-content: center;
  min-height: 700px;
  align-items: center !important;
  background-color: #F5F5F5;
}

@media only screen and (max-width: 767px) {
  .singl {
    padding: 0% !important;
  }
}

.singl h1, .singl p {
  padding-right: 50%;
}

@media only screen and (max-width: 767px) {
  .singl h1, .singl p {
    padding-right: 40%;
  }
}

.singl p span {
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}

.sing-main-text {
  padding-top: 30px;
  padding-bottom: 45px;
}

.nav-line {
  width: 100%;
  height: 100px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
}

.nav-line__modul {
  text-align: center;
}

.nav-line__next {
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .nav-line {
    display: none;
  }
}

.nav-line__prev a, .nav-line__next a {
  text-transform: uppercase !important;
  font-size: 14px !important;
  font-family: 'Montserrat' !important;
  font-weight: 500 !important;
}
