.main-footer {
	background-color: #0C0C0C;
	color: #ffffff;
	padding:  80px 0;
	&__inf {
		@include r(768) {
			text-align: center; } }

	&__menu {
		@include sm {
			padding: 35px 0; }
		@include r(768) {
			margin: 35px 0; }
		nav {
			ul {
				width: 50%;
				float: left; } } } }

.main-footer__menu,.main-footer__link {
	nav {
		text-align: left;
		@include r(768) {
			text-align: center; }
		ul {
			li {
				a {
					color: #ffffff !important; } } } } }
